import SearchMovieApi from './themoviedb-api-class'; //  
import filmCardTemplate from '../templates/card_film.hbs';

searchForm = document.querySelector('#search-form');
cardBox = document.querySelector('.js-card-collection');
const searchMovieApi = new SearchMovieApi(); //   
searchForm.addEventListener('submit', onSearch);

async function onSearch(evt) {
  evt.preventDefault();
  try {
    searchMovieApi.resetPage();
    searchMovieApi.quary = searchForm.searchQuery.value.trim(); //     id    id  searchMovieApi.movieId

    if (!searchMovieApi.quary) {
      console.log('  !');
      resetMarkup();
      return;
    }

    const { page, results, total_pages, total_results } =
      await searchMovieApi.getMovieByName(); //       getPopularFilms()/getInfoByMovieId()      

    if (total_results === 0) {
      resetMarkup();
      console.log('  !');
      return;
    }

    resetMarkup();
    createCardMarkup(results);
  } catch (error) {
    console.log(error.message);
  }
}

function createCardMarkup(arr) {
  const markup = filmCardTemplate(arr);
  cardBox.insertAdjacentHTML('beforeend', markup);
}

function resetMarkup() {
  cardBox.innerHTML = '';
}
